<template>
    <v-container grid-list-md>
        <v-card >
                    <v-card-title primary-title>
                        <h3>Ordenar</h3> 
                    </v-card-title>
                    <v-flex xs12>
                        <v-btn-toggle v-model="text">
                            <v-btn flat value="right" @click="az(2)">
                                A-Z
                            </v-btn>
                            <v-btn flat value="justify" @click="za(2)">
                                Z-A
                            </v-btn>
                        </v-btn-toggle>
                    </v-flex>
                </v-card>
        <br><br>
        <v-layout wrap>
            
            <v-flex sm12>
                <v-layout wrap>
                    <v-flex sm3 v-for="(item,index) of productoresState" :key="index">
                        <!--{{index}} - {{item.name}}>-->
                        <v-hover>
                            <v-card 
                            slot-scope="{ hover }"
                            :class="`elevation-${hover ? 12 : 2}`"
                            class="mx-auto"
                            >
                                <v-img
                                    src="https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                                    aspect-ratio="1.80"
                                >
                                    
                                </v-img>
                                <v-card-title primary-title>
                                    <h3>
                                        {{item.nombre}}
                                    </h3>
                                </v-card-title>
                                <v-card-text  aspect-ratio="0.2">
                                    <p color="green">{{item.descripcion}}</p>
                                    
                                </v-card-text>
                                <v-card-actions>
                                    <v-btn flat color="green">Ver detalles</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
        
    </v-container>
</template>

<script>


import axios from "axios"
  import {mapMutations, mapState} from "vuex"

export default {
    name:'productos',
    data() {
        return {
            
            productos: [],
            productores:[],
            text:'',
        }
    },
    methods: {
        ...mapMutations(['mostrarLoading', 'ocultarLoading', 'falso', 'setProductores','az','za','inicializaCarrito','setProductos']),
        async getProductores() {
          

          try {
            this.mostrarLoading({
              titulo: 'Accediendo a informacion',
              color: 'secondary'
            })
            axios.create({headers: {'Access-Control-Allow-Origin':'*' }})
            let datos = await axios.get('http://apis-servicio.cernet.com.mx/productor/productores')
            console.log(datos.data)
            this.setProductores(datos.data)
            this.setProductos(JSON.parse(localStorage.getItem('carrito')))
          }catch (error) {
            console.log(error)
          }finally {
            this.ocultarLoading()
          }
      },
      
    },
    created() {
        this.getProductores();
         this.falso(true)
         //console.log(localStorage.carrito)
         this.inicializaCarrito()
         
         
    },
    computed : {
        ...mapState(['productoresState']),
       
    }
}
</script>

<style>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .5;
  position: absolute;
  width: 100%;
}
</style>

